"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Package, ShoppingCart, Users, DollarSign } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { dashboardAPI, getCurrentUser, authAPI } from "@/lib/api"
import { useToast } from "@/hooks/use-toast"

export default function AdminDashboard() {
  const router = useRouter()
  const { toast } = useToast()
  const [stats, setStats] = useState({
    total_revenue: 0,
    total_orders: 0,
    total_products: 0,
    total_customers: 0,
  })
  const [recentOrders, setRecentOrders] = useState<any[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Check authentication
    const user = getCurrentUser()
    if (!user || !user.is_admin) {
      router.push("/login")
      return
    }

    // Fetch dashboard stats
    const fetchStats = async () => {
      try {
        const data = await dashboardAPI.getStats()
        setStats({
          total_revenue: data.total_revenue,
          total_orders: data.total_orders,
          total_products: data.total_products,
          total_customers: data.total_customers,
        })
        setRecentOrders(data.recent_orders || [])
      } catch (error: any) {
        if (error.message.includes("401") || error.message.includes("403")) {
          authAPI.logout()
          router.push("/login")
        } else {
          toast({
            title: "Error",
            description: "Failed to load dashboard data",
            variant: "destructive",
          })
        }
      } finally {
        setIsLoading(false)
      }
    }

    fetchStats()
  }, [router, toast])

  const statsData = [
    { label: "Total Revenue", value: `$${stats.total_revenue.toLocaleString()}`, change: "", icon: DollarSign },
    { label: "Orders", value: stats.total_orders.toString(), change: "", icon: ShoppingCart },
    { label: "Products", value: stats.total_products.toString(), change: "", icon: Package },
    { label: "Customers", value: stats.total_customers.toString(), change: "", icon: Users },
  ]

  if (isLoading) {
    return (
      <div className="p-4 sm:p-6 lg:p-8 flex items-center justify-center min-h-screen">
        <p className="text-[#2E5E99]">Loading...</p>
      </div>
    )
  }
  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-6 sm:mb-8">
        <h1 className="text-2xl sm:text-3xl font-bold text-[#0D2440]">Dashboard</h1>
        <p className="text-sm sm:text-base text-[#2E5E99]" style={{ fontFamily: "var(--font-body)" }}>
          Welcome back! Here's what's happening with your store.
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4 lg:gap-6 mb-6 sm:mb-8">
        {statsData.map((stat) => (
          <Card key={stat.label} className="bg-white border-[#7BA4D0]/30">
            <CardContent className="p-4 sm:p-6">
              <div className="flex items-center justify-between mb-3 sm:mb-4">
                <div className="w-10 h-10 sm:w-12 sm:h-12 rounded-full bg-[#2E5E99]/10 flex items-center justify-center">
                  <stat.icon className="h-5 w-5 sm:h-6 sm:w-6 text-[#2E5E99]" />
                </div>
                {stat.change && (
                  <span
                    className="text-xs sm:text-sm text-green-600 font-medium"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    {stat.change}
                  </span>
                )}
              </div>
              <p
                className="text-xs sm:text-sm text-[#2E5E99] mb-0.5 sm:mb-1"
                style={{ fontFamily: "var(--font-body)" }}
              >
                {stat.label}
              </p>
              <p className="text-lg sm:text-2xl font-bold text-[#0D2440]">{stat.value}</p>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Recent Orders */}
      <Card className="bg-white border-[#7BA4D0]/30">
        <CardHeader className="pb-3 sm:pb-4">
          <CardTitle className="text-lg sm:text-xl text-[#0D2440]">Recent Orders</CardTitle>
        </CardHeader>
        <CardContent className="px-0 sm:px-6">
          {/* Mobile Card View */}
          <div className="sm:hidden space-y-3 px-4">
            {recentOrders.length === 0 ? (
              <p className="text-center text-[#2E5E99] py-8">No recent orders</p>
            ) : (
              recentOrders.map((order) => (
                <div key={order.id} className="bg-[#E7F0FA]/50 rounded-lg p-4">
                  <div className="flex justify-between items-start mb-2">
                    <div>
                      <p className="font-medium text-[#0D2440]">{order.customer}</p>
                      <p className="text-xs text-[#2E5E99]">#{order.id}</p>
                    </div>
                  <span
                    className={`px-2 py-1 text-xs rounded-full ${
                      order.status === "Delivered"
                        ? "bg-green-100 text-green-800"
                        : order.status === "Shipped"
                          ? "bg-blue-100 text-blue-800"
                          : "bg-yellow-100 text-yellow-800"
                    }`}
                  >
                    {order.status}
                  </span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm text-[#2E5E99]">${order.total}</span>
                    <button className="text-[#2E5E99] text-sm hover:underline">View</button>
                  </div>
                </div>
              ))
            )}
          </div>

          {/* Desktop Table View */}
          <div className="hidden sm:block overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-[#7BA4D0]/20">
                  <th
                    className="text-left py-3 px-4 text-sm font-medium text-[#2E5E99]"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    Order ID
                  </th>
                  <th
                    className="text-left py-3 px-4 text-sm font-medium text-[#2E5E99]"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    Customer
                  </th>
                  <th
                    className="text-left py-3 px-4 text-sm font-medium text-[#2E5E99]"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    Total
                  </th>
                  <th
                    className="text-left py-3 px-4 text-sm font-medium text-[#2E5E99]"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    Status
                  </th>
                  <th
                    className="text-left py-3 px-4 text-sm font-medium text-[#2E5E99]"
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                {recentOrders.length === 0 ? (
                  <tr>
                    <td colSpan={5} className="py-8 text-center text-[#2E5E99]">
                      No recent orders
                    </td>
                  </tr>
                ) : (
                  recentOrders.map((order) => (
                  <tr key={order.id} className="border-b border-[#7BA4D0]/10 last:border-0">
                    <td className="py-4 px-4 text-[#0D2440]" style={{ fontFamily: "var(--font-body)" }}>
                      #{order.id}
                    </td>
                    <td className="py-4 px-4">
                      <div>
                        <p className="font-medium text-[#0D2440]">{order.customer}</p>
                        <p className="text-sm text-[#2E5E99]" style={{ fontFamily: "var(--font-body)" }}>
                          {order.email}
                        </p>
                      </div>
                    </td>
                    <td className="py-4 px-4 text-[#0D2440]" style={{ fontFamily: "var(--font-body)" }}>
                      ${order.total}
                    </td>
                    <td className="py-4 px-4">
                      <span
                        className={`px-2 py-1 text-xs rounded-full ${
                          order.status === "Delivered"
                            ? "bg-green-100 text-green-800"
                            : order.status === "Shipped"
                              ? "bg-blue-100 text-blue-800"
                              : "bg-yellow-100 text-yellow-800"
                        }`}
                        style={{ fontFamily: "var(--font-body)" }}
                      >
                        {order.status}
                      </span>
                    </td>
                    <td className="py-4 px-4">
                      <button
                        className="text-[#2E5E99] hover:underline text-sm"
                        style={{ fontFamily: "var(--font-body)" }}
                      >
                        View
                      </button>
                    </td>
                  </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
